.App {
  text-align: center;
  align-content: center;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color:  #008F11;
  height: 10vh;
  display: block !important;
  width: 100% !important;


}
.header-child{
  background-color: #003B00;
  height: 3vh;



}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main_page{
  display: inline-block;
  border: 2px solid #003B00;
  border-radius: 5px;
  min-height: 100vh;
  margin: 1rem;
  width: 100%;
  max-width: 50rem;
  min-width: 15rem;
  text-align: center;

}

.screen-wrapper{
  border: 1px dashed #003B00;
  margin: 1rem;
  height: 20vh;
  padding: 1rem;
  margin-left: 2rem;
  margin-right: 2rem;
  color: #00FF41;
  text-align: left;
  font-size: smaller;
  word-wrap: break-word;
}

.f-logo{
  width: 100% !important;
}


.s-bar{
  border: 1px dashed #008F11;
  margin: 1rem;
  margin-left: 2rem;
  margin-right: 2rem;
}

.buttons-wrapper{
  margin: 1rem;
  height: 6vh;
  font-size: smaller;
}
.generate-btn, .copy-btn{
  width: 48%;
  height: 100% !important;
  display: flex;
  font-weight: 500;
  padding-top: 17px;
  max-height: 5vh;
  cursor: pointer;


}
.generate-btn:active, .copy-btn:active{
  color: black !important;
}

.generate-btn:hover, .copy-btn:hover{
  opacity: 0.9;
  color: white;
}
.generate-btn{

  display: inline-block;
  background-color: #29B65A;
}
.copy-btn{
  display: inline-block;

  background-color: #84D08B;
}
.footer-content{


  margin: 1rem;
  margin-top: 2rem;
  padding: 0 !important;
  color: #008F11;



}
.footer-content-2{
  margin: 1rem;
  margin-top: 3rem;
  height: 37vh;

}